// =========================
// Headings and general styles
// =========================

* {
  font-family: "Roboto", sans-serif;
  font-weight: 100;
}

.heading-primary {
  font-family: "Dancing Script", serif;
  font-weight: 700;
  font-size: 5.8rem;
  color: $color-gold;
  text-shadow: 0 2rem 4rem rgba($color-gold, 0.2);
  margin-top: 8rem;
  margin-bottom: 2.2rem;
  text-align: center;

  &--sub {
    font-family: "Roboto Condensed", sans-serif;
    font-weight: normal;
    font-size: 1.9rem;
    color: $color-bg;
    text-shadow: 0 1rem 4rem rgba($color-bg, 0.2);
    text-transform: uppercase;
    text-align: center;
    max-width: 470px;
    margin-bottom: 4rem;
  }
}

.heading-section-title {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 600;
  font-size: 3rem;
  color: $color-blue-light;
  text-align: center;
  margin-bottom: 1rem;
}

.heading-section--subtitle {
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  font-size: 2rem;
  color: $color-blue-dark;
  text-align: center;
}

.paragraph {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 1.4rem;
  color: $color-bg;

  &--inverted {
    color: $color-blue-light;
  }

  &--large {
    font-size: 1.6rem;
  }
}

.testimonial {
  &__title {
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-size: 2rem;
    color: $color-blue-dark;
  }

  &__rating {
    color: $color-gold;
  }

  &__review {
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-size: 1.8rem;
    color: $color-blue-light;
  }

  &__name {
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-size: 1.4rem;
    text-transform: uppercase;
    color: $color-blue-light;
  }

  &__date {
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-size: 1.2rem;
    color: gray;
  }
}

.newsletter {
  &__heading {
    font-family: "Roboto Condensed", sans-serif;
    font-weight: 800;
    font-size: 5rem;
    text-transform: uppercase;
    letter-spacing: -1px;
    color: $color-gold;
  }

  &__subheading {
    font-family: "Roboto", sans-serif;
    font-weight: 200;
    font-size: 2.3rem;
    color: $color-bg;
    margin-bottom: 1rem;
  }
}

.coffee__price {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 800;
  font-size: 2.2rem;
  color: $color-blue-dark;
}

.coffee__info {
  font-weight: 100;
  font-size: 1.8rem;
  color: $color-bg;

  &--key {
    color: $color-blue-light;
  }
}

.coffee__flavour-tag {
  text-align: center;

  font-weight: 100;
  font-size: 1.8rem;
  color: $color-bg;
}

// =========================
// Sidebar
// =========================

.sidebar__link {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
  color: $color-bg;
  text-decoration: none;
  outline: none;
  text-transform: uppercase;
  transition: all 0.3s;

  &:hover {
    color: $color-gold;
  }

  &:active {
    text-shadow: 0 1rem 3rem rgba($color-gold, 0.3);
    transform: translate(1px, 2px);
  }

  &--inverted {
    color: $color-gold;

    &:hover {
      color: $color-bg;
    }

    &:active {
      text-shadow: 0 1rem 3rem rgba($color-bg, 0.3);
      transform: translate(1px, 2px);
    }
  }
}

.fa-shopping-cart {
  display: none; // Hide unless logged in
  margin-top: 1.3rem;
  color: $color-bg;
}

.sidebar__brand {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 800;
  font-size: 1.4rem;
  text-transform: uppercase;
  color: $color-gold;
}

.sidebar-paragraph {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 1.1rem;
  color: $color-bg;
}

.liam {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 800;
  font-size: 1.2rem;
  letter-spacing: -1px;
  color: $color-gold;
}

// =========================
// Product grid
// =========================
.product {
  &__title {
    flex: 100%;
    font-family: "Roboto Condensed", sans-serif;
    font-weight: 500;
    font-size: 2.4rem;
    letter-spacing: -1.5px;
    color: $color-blue-light;
    text-align: center;
    margin-bottom: 0.5rem;
  }

  &__description--short {
    flex: 100%;
    font-family: "Roboto", sans-serif;
    font-weight: normal;
    font-size: 1.2rem;
    color: $color-blue-dark;
    margin-bottom: 2rem;
  }

  &__price {
    flex: 25%;
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-size: 1.8rem;
    text-align: right;
    color: $color-blue-dark;
  }
}

// ==========================
// Forms
// ==========================

.form {
  &__label {
    font-family: inherit;
    color: $color-blue-dark;
    font-size: 1.8rem;
  }

  &__input,
  &__textarea,
  &__dropdown {
    color: $color-blue-dark;
    font-size: 1.8rem;
  }

  &__legend {
    color: $color-blue-dark;
    font-size: 1.8rem;
  }

  &__span {
    display: block;
    font-size: 1.6rem;
    color: $color-blue-dark;
  }
}

// ==========================
// Coffee info table
// ==========================
.coffee__key {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 1.4rem;
  text-transform: uppercase;
  color: $color-blue-dark;
}

.coffee__value {
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 1.4rem;
  color: $color-blue-lighter;
}



